input[type="text"],
select,
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  resize: vertical;
}

/* Style the label to display next to the inputs */
label {
  padding: 12px 12px 12px 0;
  display: inline-block;
}

/* Style the submit button */
input[type="submit"] {
  background-color: #04aa6d;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  float: right;
}

/* Style the container */
.container {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

/* Floating column for labels: 25% width */
.col-25 {
  float: left;
  width: 25%;
  margin-top: 6px;
}

/* Floating column for inputs: 75% width */
.col-75 {
  float: left;
  width: 75%;
  margin-top: 6px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 960px) {
  .col-25,
  .col-75,
  input[type="submit"] {
    width: 100%;
    margin-top: 0;
  }

  .lead-mid-content {
    padding: 0;
    margin: 0;
    display: flex;
    
  }

  .text-content {
    display: flex;
    justify-content: center;
    padding-left: 0rem;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
    box-shadow: 5px 3px 10px lightgray;
  }

  .first-text {
    font-size: 2rem;
    margin-bottom: 10px;
  }

  .second-text {
    font-size: 1.5rem;
  }

  .qr-code {
    height: 100%;
    width: 100vw;
    display: flex;
    justify-content: center;
    padding: 0;
    margin: 0;
    align-items: center;
  }

  .qr-img {
    height: 7rem;
  }
}

@media screen and (max-width: 300px) {
  .first-text {
    font-size: 1.5rem;
  }

  .second-text {
    font-size: 1rem;
  }
}

